body {
  background-color: #1e555c;
  color: white;
  font-size: 30px;
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-title {
  font-size: 50px;
  margin-bottom: 20px;
}

.length-control {
  display: inline-block;
  width: 250px;
}

.btn-level {
  display: inline-block;
}

button {
  color: white;
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
}

.btn-level i {
  font-size: 26px;
}

.btn-level:nth-child(2) {
  width: 40px;
}

.timer {
  border: 7px solid #13353a;
  margin: 20px auto 10px auto;
  width: 270px;
  height: 160px;
  border-radius: 50px;
  position: relative;
}

.timer-wrapper {
  position: absolute;
  width: 190px;
  height: 110px;
  left: 50%;
  top: 50%;
  margin-left: -95px;
  margin-top: -70px;
}

#time-left {
  font-size: 80px;
}

.size2x {
  font-size: 2rem;
}